import { Component, OnInit, Inject } from '@angular/core';

import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { FuseConfigService } from '@fuse/services/config.service';
import { Router } from '@angular/router';

import { locale as spanish } from './i18n/es';
import { ElementsService } from './elements.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';
import { FuseProgressBarService } from '@fuse/components/progress-bar/progress-bar.service';

@Component({
    selector   : 'elements',
    templateUrl: './elements.component.html',
    styleUrls  : ['./elements.component.scss']
})
export class ElementsComponent implements OnInit {

    loading: boolean = true;
    elements: Array<object> = [];

    public horizontalPos: MatSnackBarHorizontalPosition = 'center';
    public verticalPos: MatSnackBarVerticalPosition = 'top';

    constructor(
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private _fuseConfigService: FuseConfigService,
        private router: Router,
        private dialog: MatDialog,
        private elementsService: ElementsService,
        private snackbar: MatSnackBar,
        private fuseProgressBarService: FuseProgressBarService
    ) {
        this._fuseTranslationLoaderService.loadTranslations(spanish);

        this._fuseConfigService.config = {
            layout: {
               navbar: {
                    hidden: false
                },
                toolbar: {
                    hidden: false
                }
            }
        }
    }

    ngOnInit() {
        this.fuseProgressBarService.show();
        this.getElements();
    }

    getElements() {
        this.elementsService.getElementList().subscribe(res => {
            this.elements = res;
            this.loading = false;
            this.fuseProgressBarService.hide();
        })
    }

    deleteElement(id) {
        
        const dialog = this.dialog.open(DeleteDialogComponent, {
            data: {
            }
        });

        dialog.afterClosed().subscribe(res => {
            this.fuseProgressBarService.show();
            if(typeof res !== 'undefined') {
                this.elementsService.deleteElement(id).subscribe(res => {
                    if(res.status == 500) {
                        this.snackbar.open(res.message, 'ERROR', {
                            horizontalPosition: this.horizontalPos,
                            verticalPosition: this.verticalPos,
                            duration: 5000,
                            panelClass: 'red'
                        });
                    } else {
                        this.snackbar.open(res.message, 'INFO', {
                            horizontalPosition: this.horizontalPos,
                            verticalPosition: this.verticalPos,
                            duration: 5000,
                            panelClass: 'green'
                        });
                    }
                    this.getElements();
                }, err => {
                    this.snackbar.open(err.error.message, 'ERROR', {
                        horizontalPosition: this.horizontalPos,
                        verticalPosition: this.verticalPos,
                        duration: 5000,
                        panelClass: 'red'
                    });
                    this.fuseProgressBarService.hide();
                })
            }
        })

    }

    viewElement(id) {
        this.router.navigate(['elements/detail/' + id]);
    }
}

@Component({
    selector: 'delete-dialog',
    templateUrl: './delete-dialog/delete-dialog.component.html',
})
export class DeleteDialogComponent implements OnInit {

    constructor(
        public dialogRef: MatDialogRef<any>,
        private router: Router,
        @Inject(MAT_DIALOG_DATA) public data: any
    ) {}

    ngOnInit(): void {

    }

    cancel(): void {
        this.dialogRef.close();
    }

    delete(): void {
        
    }
}