import { Component, OnInit, ViewChild, Inject, OnDestroy } from '@angular/core';

import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { FuseConfigService } from '@fuse/services/config.service';
import { Router } from '@angular/router';

import { locale as spanish } from '../i18n/es';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ElementsService } from '../elements.service';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';
import { TranslateService } from '@ngx-translate/core';
import { FuseProgressBarService } from '@fuse/components/progress-bar/progress-bar.service';
import { COMMA, ENTER, SEMICOLON } from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material/chips';

import * as moment from 'moment';

@Component({
    selector   : 'create',
    templateUrl: './create.component.html',
    styleUrls  : ['./create.component.scss']
})
export class CreateComponent implements OnInit, OnDestroy {

    @ViewChild('file', { static: false }) file: any;
    @ViewChild('toggle', { static: true }) toggle: any;

    form: FormGroup;
    searchForm: FormGroup;

    image;
    imagePath;
    import: boolean = false;

    defaultImg = 'assets/images/placeholder.png'

    readonly separatorKeysCodes: number[] = [ENTER, COMMA, SEMICOLON];
    materiality = [];
    levels = [{ value: 'Fundational', acc_value: 'Foundational', name: null },
        { value: 'Intermediate', acc_value: 'Intermediate', name: null },
        { value: 'Advanced', acc_value: 'Advanced', name: null }]
    languages = [{ value: 'es', name: null }, { value: 'en', name: null }, { value: 'pt', name: null },
        { value: 'fr', name: null }, { value: 'other', name: null }]
    skills = [];

    badges = JSON.parse(sessionStorage.getItem('badges'));
    badgesToShow = JSON.parse(sessionStorage.getItem('badges'));

    public horizontalPos: MatSnackBarHorizontalPosition = 'center';
    public verticalPos: MatSnackBarVerticalPosition = 'top';

    constructor(
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private _fuseConfigService: FuseConfigService,
        private router: Router,
        private formBuilder: FormBuilder,
        private elementsService: ElementsService,
        private snackbar: MatSnackBar,
        private translate: TranslateService,
        private dialog: MatDialog,
        private fuseProgressBarService: FuseProgressBarService
    ) {
        this._fuseTranslationLoaderService.loadTranslations(spanish);

        this._fuseConfigService.config = {
            layout: {
                navbar: {
                    hidden: false
                },
                toolbar: {
                    hidden: false
                }
            }
        }
    }

    ngOnInit() {
        this.cleanData();
        this.elementsService.elementName = this.translate.instant('CREATE.NEW_ELEMENT');
        this.levels.forEach(level => level.name = this.translate.instant('LEVEL.'.concat(level.acc_value)));
        this.languages.forEach(lang => lang.name = this.translate.instant('LANGUAGES.'.concat(lang.value)));

        this.searchForm = this.formBuilder.group({
            search: ['']
        });

        this.searchChanged();
        
        this.getMateriality();
    }

    displayCalendar() {
        this.toggle.datepicker.opened = true;
    }

    getMateriality() {
        this.elementsService.getMateriality().subscribe(res => {
            res.forEach(el => this.materiality.push({ value: el.descripcion, name: this.translate.instant('MATERIALITY.'.concat(el.descripcion)) }))
        })
    }

    add(event: MatChipInputEvent): void {
        const input = event.input;
        const value = event.value;
        
        if ((value || '').trim()) {
            this.skills.push(value.trim());
        }
        if (input) {
            input.value = '';
        }
    }

    remove(skill): void {
        const index = this.skills.indexOf(skill);
    
        if (index >= 0) {
            this.skills.splice(index, 1);
        }
    }

    migrateData(badge) {

        this.cleanData();

        this.form.patchValue({
            name: badge.name,
            level: badge.level,
            description: badge.description,
            criterion: badge.badge_template_activities.map(badge => badge.title).join(),
            badge_id: badge.id
        });

        this.form.markAllAsTouched();

        this.image = badge.image_url;
        this.skills = badge.skills;

        this.import = true;
    }

    create() {
        this.fuseProgressBarService.show();

        const body = {
            name: this.form.get('name').value,
            issuer: this.form.get('issuer').value,
            issuer_url: this.form.get('issuer_url').value !== "" ? this.form.get('issuer_url').value : undefined,
            category: this.form.get('category').value,
            duration: this.form.get('duration').value ? parseInt(this.form.get('duration').value) : undefined,
            description: this.form.get('description').value,
            skills: this.skills.join(';'),
            criterion: this.form.get('criterion').value,
            materiality: this.form.get('materiality').value,
            level: this.levels.find(l => l.acc_value == this.form.get('level').value).value,
            score: parseInt(this.form.get('score').value),
            expire_at: this.form.get('expire_at').value !== "" ? moment(this.form.get('expire_at').value, 'DD/MM/YYYY').format('YYYY-MM-DD') : undefined,
            language: this.form.get('language').value,
            image_url: this.import ? this.image : '',
            badge_id: this.form.get('badge_id').value === '' ? undefined : this.form.get('badge_id').value,
        };

        this.elementsService.postElement(body).subscribe(res => {
            if(!this.import) {
                let archivo: any = new FormData();
                archivo.append('archivo', this.file.nativeElement.files[0]);
                
                this.elementsService.uploadImage(archivo, res.id).subscribe(_res => {
                    this.snackbar.open(res.message, 'INFO', {
                        horizontalPosition: this.horizontalPos,
                        verticalPosition: this.verticalPos,
                        duration: 5000,
                        panelClass: 'green',
                    });
        
                    this.fuseProgressBarService.hide();
        
                    this.router.navigate(['elements/consult']);
                }, err => {
                    console.log(err);
                    this.snackbar.open(err.error.message, 'ERROR', {
                        horizontalPosition: this.horizontalPos,
                        verticalPosition: this.verticalPos,
                        duration: 5000,
                        panelClass: 'red',
                    });

                    this.elementsService.deleteElement(res.id).subscribe(_res => {})
                    this.fuseProgressBarService.hide();
                })
            } else {
                this.snackbar.open(res.message, 'INFO', {
                    horizontalPosition: this.horizontalPos,
                    verticalPosition: this.verticalPos,
                    duration: 5000,
                    panelClass: 'green',
                });
    
                this.fuseProgressBarService.hide();
    
                this.router.navigate(['elements/consult']);
            }
            
        }, err => {
            this.snackbar.open(err.error.message, 'ERROR', {
                horizontalPosition: this.horizontalPos,
                verticalPosition: this.verticalPos,
                duration: 5000,
                panelClass: 'red'
            });

            this.fuseProgressBarService.hide();
        })
    }

    cancelCreate() {
        this.cleanData();
        this.image = null;
        this.imagePath = null;
        this.file.nativeElement.value = '';
        this.import = false;
    }

    cleanData() {
        this.form = this.formBuilder.group({
            name: ['', [Validators.required, Validators.minLength(5)]],
            issuer: ['', [Validators.required, Validators.maxLength(20)]],
            issuer_url: ['', Validators.pattern(/(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/)],
            category: ['', Validators.maxLength(20)],
            duration: [null, Validators.pattern('^[0-9]+$')],
            description: ['', Validators.maxLength(500)],
            criterion: [''],
            materiality: ['', [Validators.required]],
            level: ['', Validators.required],
            score: [0, [Validators.required, Validators.pattern('^[0-9]+$')]],
            expire_at: [''],
            language: ['', [Validators.required]],
            badge_id: ['']
        });
        // this.form.get('issuer').markAsTouched();
        // this.form.get('issuer_url').markAsTouched();

        this.skills = [];
    }

    ngDoCheck() {
        // if(this.form.get('issuer').value) {
        //     this.form.get('issuer_url').setValidators([Validators.required, Validators.pattern(/(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/)]);
        // } else {
        //     this.form.get('issuer_url').setValidators(Validators.pattern(/(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/));
        // }

        // if(this.form.get('issuer_url').value) {
        //     this.form.get('issuer').setValidators(Validators.required);
        // } else {
        //     this.form.get('issuer').setValidators(Validators.maxLength(20));
        // }
        // this.form.get('issuer_url').updateValueAndValidity();
        // this.form.get('issuer').updateValueAndValidity();
    }

    searchChanged() {
        this.searchForm.valueChanges.subscribe(form => {
            if(form.search === '') this.badgesToShow = this.badges;
            else {
                this.badgesToShow = [];
                this.badges.forEach(badge => {
                    if(badge.name.toLowerCase().includes(form.search.toLowerCase())) this.badgesToShow.push(badge);
                })
            }
        });
    }

    preview(files) {
        if (files.length !== 1) return;

        if (files[0].type !== 'image/png') {
            this.snackbar.open(this.translate.instant('CREATE.IMAGE_TYPE_ERROR'), 'ERROR', {
                horizontalPosition: this.horizontalPos,
                verticalPosition: this.verticalPos,
                duration: 5000,
                panelClass: 'red',
            });
            return;
        }
        
        if (files[0].size > 2097152) {
            this.snackbar.open(this.translate.instant('CREATE.MAX_FILE_SIZE'), 'ERROR', {
                horizontalPosition: this.horizontalPos,
                verticalPosition: this.verticalPos,
                duration: 5000,
                panelClass: 'red',
            });
            // this.data.imgURL = null;
            return;
        }
     
        var reader = new FileReader();
        this.imagePath = files;
        reader.readAsDataURL(files[0]);
        reader.onload = (_event) => {
            this.image = reader.result;
        }
    }

    removeImage() {
        this.file.nativeElement.value = '';
        this.image = null;
        this.imagePath = null;
    }

    goHome() {

        const dialog = this.dialog.open(CancelDialogComponent, {
            data: {
            }
        });
        
        dialog.afterClosed().subscribe(res => {
            if(typeof res !== 'undefined') this.router.navigate(['elements/consult']);
        })
    }

    ngOnDestroy() {
        this.elementsService.elementName = '';
    }
}

@Component({
    selector: 'cancel-dialog',
    templateUrl: '../cancel-dialog/cancel-dialog.component.html',
})
export class CancelDialogComponent implements OnInit {

    constructor(
        public dialogRef: MatDialogRef<any>,
        private router: Router,
        @Inject(MAT_DIALOG_DATA) public data: any
    ) {}

    ngOnInit(): void {

    }

    cancel(): void {
        this.dialogRef.close();
    }

    goHome(): void {
        
    }
}