import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';

import { FuseSharedModule } from '@fuse/shared.module';

import { MatButtonModule } from '@angular/material/button';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatDialogModule } from '@angular/material/dialog';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatTooltipModule } from '@angular/material/tooltip';

import { ElementsComponent, DeleteDialogComponent } from './elements.component';
import { ElementComponent } from './element/element.component';
import { CreateComponent, CancelDialogComponent } from './create/create.component';
import { DetailComponent } from './detail/detail.component';
import { MatChipsModule } from '@angular/material/chips';

const routes = [
    {
        path: 'consult',
        component: ElementsComponent
    },
    {
        path: 'create',
        component: CreateComponent
    },
    {
        path: 'detail/:elementId',
        component: DetailComponent
    }
];

@NgModule({
    declarations: [
        ElementsComponent,
        ElementComponent,
        CreateComponent,
        DetailComponent,
        CancelDialogComponent,
        DeleteDialogComponent
    ],
    imports     : [
        RouterModule.forChild(routes),

        TranslateModule,

        FuseSharedModule,

        MatInputModule,
        MatSelectModule,
        MatButtonModule,
        MatTooltipModule,
        MatIconModule,
        MatSidenavModule,
        MatSnackBarModule,
        MatDialogModule,
        MatDatepickerModule,
        MatChipsModule
    ],
    exports     : [
        ElementsComponent,
        CreateComponent,
        DetailComponent
    ],
    entryComponents: [CancelDialogComponent, DeleteDialogComponent]
})

export class ElementsModule
{
}