import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { FuseConfigService } from '@fuse/services/config.service';
import { ElementsService } from '../elements.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'element',
  templateUrl: './element.component.html',
  styleUrls: ['./element.component.scss']
})
export class ElementComponent implements OnInit {
  
  @Input('element') element;
  url;
  date;

  @Output() view: EventEmitter<any> = new EventEmitter();
  @Output() delete: EventEmitter<any> = new EventEmitter();

  defaultImg = 'assets/images/placeholder.png'

  constructor(private service: ElementsService, private changesDetector: ChangeDetectorRef
    ) { }

  ngOnInit() {
    if(this.element.image_url.includes('http')) {
      this.url = this.element.image_url;
    } else {
      this.service.getImage(this.element.id).subscribe(res => {
        var reader = new FileReader();
        reader.readAsDataURL(res);
        reader.onload = (event) => {
          this.url = reader.result;
        }
      })
    }
  }

  ngAfterViewChecked() {
    this.changesDetector.detectChanges();
  }

  getTimestamp() {
    if(!this.date) this.date = Date.now();
    return this.date;
  }

  getEllipsis(str: string) {
    if(str.length < 70) return str; return str.substring(0, 70).trim() + '...';
  }

}